html {
  box-sizing: border-box;
}

*, ::before, ::after {
  box-sizing: inherit;
}

body {
  max-width: 500px;
  margin: 0 auto;
  padding: 0 20px;
  line-height: 1.5;
  text-align: center;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-optical-sizing: auto;
  background-color: white;
}

.header__inner {
  position: relative;
  display: flex;
  justify-content: right;
  align-items: center;
  margin-top: 30px;
}
.header__logo {
  width: 20%;
  position: absolute;
  left: 15px;
  top: 15px;
}

.menu__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}
.menu__link {
  display: block;
  padding: 10px;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 16px;
  text-decoration: none;
  color: #FF892C;
}
@media screen and (max-width: 400px) {
  .menu__link {
    padding: 5px;
  }
}
.menu__link:hover {
  color: black;
}
.menu__item {
  position: relative;
}
.menu__item:hover .menu__submenu {
  display: block;
}
.menu__submenu {
  display: none;
  position: absolute;
  background-color: white;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
  list-style: none;
  padding: 0.5em;
}
.menu__submenu__toggle {
  display: none;
}
.menu__submenu__:checked ~ .menu__submenu__submenu {
  display: block;
}

.hero-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  object-position: 0 0;
}

.main__a1-title, .main__a2-title {
  padding-top: 50px;
  padding-bottom: 20px;
  margin: 0;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 18px;
  color: #f77f00;
}
.main__a1-picture, .main__a2-picture {
  width: 80%;
  border: 3px solid #CC5803;
}
.main__a1-picture {
  border-radius: 0 5%;
}
.main__a2-picture {
  border-radius: 5% 0;
}
.main__button {
  padding: 5px 10px;
  border: 1px solid #FF892C;
  border-radius: 10%;
  background-color: #FF892C;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 16px;
}

.read-more {
  color: white;
  text-decoration: none;
}

.footer {
  font-size: 12px;
  color: #adb5bd;
  margin-top: 50px;
  margin-bottom: 10px;
}

.project__1-title {
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 18px;
  color: #f77f00;
  padding-top: 50px;
  padding-bottom: 30px;
  margin: 0;
}
.project__1-title::after {
  content: "";
  display: block;
  width: 60px;
  height: 2px;
  background-color: #CC5803;
  margin-top: 10px;
  margin-right: auto;
  margin-left: auto;
}
.project__1-paragraph {
  margin: 0;
}
.project__2-picture {
  width: 70%;
  border: 2px solid #CC5803;
  border-radius: 5%;
  margin-top: 50px;
  margin-bottom: 50px;
}
.project__ilustration {
  width: 40%;
}

.kontakt__1-paragraph {
  padding-top: 50px;
  padding-bottom: 20px;
  margin: 0;
  font-size: 16px;
  color: #FF892C;
}
.kontakt__1-email, .kontakt__1-linkedin, .kontakt__1-doucuji {
  margin-top: 25px;
  margin-bottom: 25px;
}
.kontakt__1-odkaz1, .kontakt__1-odkaz2, .kontakt__1-odkaz3 {
  color: #CC5803;
  text-decoration: none;
  margin-bottom: 30px;
}
.kontakt__1-message {
  background-color: #f5f3f4;
  border: 2px solid #FF892C;
  border-radius: 5%;
  margin-bottom: 30px;
}
.kontakt__button {
  padding: 5px 10px;
  margin-bottom: 16px;
  border: 2px solid #FF892C;
  border-radius: 10%;
  background-color: white;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 16px;
}
.kontakt__2-picture {
  display: block;
  margin: 0 auto;
}

.form-field {
  margin-bottom: 10px;
}

.form-field label {
  display: block;
}

.form-field input {
  width: 300px;
}

.form-field textarea {
  width: 300px;
  height: 250px;
  border-radius: 5%;
}